/*----------------------------------------
    # Theme Colours -- /_abstracts/_colours.scss
----------------------------------------*/
:root {
  --primary-main: hsl(0deg, 0%, 0%) ;
  --primary-highlight: hsl(0deg, 0%, 50%) ;
  --primary-invert: hsl(100deg, 100%, 100%) ;
  --secondary-main: hsl(0deg, 0%, 20%) ;
  --secondary-highlight: hsl(0deg, 0%, 40%) ;
  --secondary-invert: hsl(0deg, 0%, 100%) ;
  --tertiary-main: hsl(0deg, 0%, 10%) ;
  --tertiary-highlight: hsl(0deg, 0%, 40%) ;
  --tertiary-invert: hsl(0deg, 0%, 100%) ;
  --quaternary-main: hsl(70deg, 31%, 85%) ;
  --quaternary-highlight: hsl(70deg, 20%, 60%) ;
  --quaternary-invert: hsl(70deg, 31%, 10%) ;
  --light-main: hsl(5deg, 2%, 100%) ;
  --light-highlight: hsl(5deg, 2%, 50%) ;
  --light-invert: hsl(5deg, 2%, 0%) ;
  --dark-main: hsl(5deg, 2%, 0%) ;
  --dark-highlight: hsl(5deg, 2%, 50%) ;
  --dark-invert: hsl(5deg, 2%, 100%) ;
}

/*----------------------------------------
    # Base Colours -- /_abstracts/_colours.scss
----------------------------------------*/
