@supports (display: grid) {
  .grid-row {
    align-items: stretch;
    display: grid;
    grid-auto-flow: dense;
    grid-auto-rows: minmax(min-content, max-content);
    grid-template-columns: [full-start] minmax(calc((100% - 1650px) / 2), 1fr) [main-start] repeat(24, [col-start] 1fr) [main-end] minmax(calc((100% - 1650px) / 2), 1fr) [full-end];
    grid-template-rows: auto;
    position: relative;
    overflow: hidden;
    grid-gap: 0;
  }
  .grid, .no-grid {
    z-index: 2;
    align-items: stretch;
    display: grid;
    grid-auto-flow: dense;
    grid-column-start: 5;
    grid-column-end: 23;
  }
